___CSS_LOADER_EXPORT___.push([module.id, `body {
  margin: 0;
  font-family: "Poppins", sans-serif;
  background: #fff;
}

/**
 * navbar
 */

.navbar {
  background: #008080;
  margin: 0;
  padding: 20px 30px;
}

.navbar .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
}

.navbar .container .logo {
  font-size: 14pt;
  font-weight: bold;
  text-decoration: none;
  color: white;
}

.navbar .container .menu {
  margin: 0;
  display: flex;
  list-style: none;
  padding: 0;
}

.navbar .container .menu li {
  padding-left: 0;
  margin-right: 10px;
}

.navbar .container .menu li a {
  padding: 10px;
  color: white;
  text-decoration: none;
  display: inline-block;
}

.tombol {
  display: none;
  color: white;
  font-size: 14pt;
}

.tombol:hover {
  cursor: pointer;
}

/**
 * main
 */

main {
  width: 100%;
  min-height: 100vh;
}

.content {
  padding: 32px;
  margin: 0;
}

/**
 * footer
 */

footer {
  background-color: #008080;
  color: white;
  text-align: center;
  padding: 20px 30px;
}
