___CSS_LOADER_EXPORT___.push([module.id, `.article-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
}

.article-content .article-headline {
  text-align: center;
  height: 50vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.articles-container .article-item {
  margin-bottom: 7rem;
}

.articles-container .article-item {
  padding: 10px 30px;
  margin-bottom: 2rem;
  border-radius: 10px;
  text-align: left;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px,
    rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
}

.articles-container .article-item .button {
  color: #008080;
  font-weight: bold;
  text-decoration: none;
}
